
<template>
  <div>
    <el-input style="width:380px;cursor: pointer;" v-model="checkedNamesTwo" :readonly="true">
      <el-button @click="initData" slot="append" icon="el-icon-search"></el-button>
    </el-input>
    <el-dialog title='商家选择' :visible="dialogVisible" width="900px" :before-close="close">
      <div class="cms-dialog__body">

     <section class="cms-list-query">
          <div class="query-group">
            <label>店铺名称</label>
            <el-input v-model="queryParams.storeName" @keyup.native.enter="query"></el-input>
          </div>
          <div class="query-group">
            <label>商家账号</label>
            <el-input v-model="queryParams.storeUsername" @keyup.native.enter="query"></el-input>
          </div>
          <div class="query-group">
             <cms-button size="small" :colorPicker='true' @click="query" style="margin-left: 50px;">查询</cms-button>
          </div>
        </section>
      <el-table :data="tableData" style="cursor: pointer;" ref='dataTable'
      @selection-change='getCheckItems'
       @row-click='checkRows' max-height='500'>
        <!-- 多选 -->
        <el-table-column width="60"  type="selection" v-if="multiple">
        </el-table-column>
        <!-- 单选 -->
        <el-table-column width="60" align="center" v-else>
          <div slot-scope="scope">
            <el-radio :label="scope.$index" v-model="selectIndex" >&nbsp;</el-radio>
          </div>
        </el-table-column>
        <el-table-column prop="id" label="id" width="60"></el-table-column>
           <el-table-column prop="storeName" label="店铺名称" min-width='160'></el-table-column>
      <el-table-column prop="storeUsername" label="商家账号" min-width="160"></el-table-column>
      <el-table-column prop="storeType" label="店铺类型" min-width="90">
        <div slot-scope="scope">
          <span class="error" v-if="scope.row.storeType === 1">个人店铺</span>
          <span class="success" v-else-if="scope.row.storeType === 2">企业店铺</span>
        </div>
      </el-table-column>
      </el-table>
      <cms-pagination @change="getPage" :total='total'></cms-pagination>
      </div>
      <span slot="footer" class="dialog-footer">
        <cms-button type="primary" @click="determine">确 定</cms-button>
        <cms-button type='info' @click="close">取 消</cms-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import {
  shopManageList
} from '@/api/shop/shopManage.js'
import list from '@/mixins/list'
export default {
  mixins: [list],
  name: 'cmsArticleCategory',
  props: {
    value: {},
    multiple: {
      type: Boolean,
      default: true
    },
    labelName: {
      type: String,
      default: 'name'
    },
    labelData: {}
  },
  data() {
    return {
      dialogVisible: false,
      checkItems: [],
      selectIndex: '', // 全选状态，默认全不选
      queryParams: {
        storeName: '',
        storeGradeId: '',
        storeType: '',
        categoryId: '',
        startTime: '',
        endTime: '',
        enabled: '',
        storeUsername: '',
        page: 1,
        size: 10,
        status: 1
      },
      checkedNames: '', // 临时显示的名称
      checkedNamesTwo: '' // 临时显示的名称
    }
  },
  watch: {
    labelData: {
      handler() {
        if (this.multiple) {
          const name = this.labelData.map(v => v[this.labelName]).toString()
          this.checkedNames = name
        } else {
          const name = this.labelData[this.labelName]
          this.checkedNames = name
        }
      }
    }
  },
  methods: {
    initData() {
      this.dialogVisible = true
      this.getTableData(shopManageList, this.queryParams)
    },
    checkRows(row) { // 单击行选中
      if (this.multiple) { // 多选时
        this.$refs.dataTable.toggleRowSelection(row)
      } else { // 单选时
        this.selectIndex = this.tableData.indexOf(row)
        this.checkItems = [row]
      }
    },
    determine() {
      this.$emit('get', this.multiple ? this.checkItems : this.checkItems[0])
      this.$emit('update:labelData', this.multiple ? this.checkItems : this.checkItems[0])
      this.$emit('input', this.checkItems.map(v => v.id))
      this.checkedNames = this.checkItems.map(v => v[this.labelName]).toString()
      this.checkedNamesTwo = this.checkItems.map(v => v[this.labelName]).toString()
      this.close()
    },
    close(done) {
      this.checkItems = []
      this.selectIndex = ''
      this.$refs.dataTable.clearSelection()
      this.dialogVisible = false
    }
  }
}
</script>

<style>
</style>




