// 消息-站内信相关接口
import axios from './../fetch'

// 站内信删除
export const mailDelete = params => { return axios.post('/admin/platform/sysmessage/delete', params) }

// 站内信列表
export const mailList = params => { return axios.get('/admin/platform/sysmessage/page', { params: params }) }

// 站内信列表
export const mailGet = params => { return axios.get('/admin/platform/sysmessage/get', { params: params }) }

// 站内信发送消息
export const mailSave = params => { return axios.post('/admin/platform/sysmessage/save', params) }



