<template>
  <div class="cms-list-layout">
    <cms-breadcrumb to="/mail/list"  @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt :collapse='false'>
      <li>会员及商家选择可支持多选操作</li>
    </cms-prompt>
      <el-form ref="form" :rules="rules" :model="formData" label-width="210px" @submit.native.prevent>
        <el-form-item label="消息类型" prop="mesType" :error='errorMessage.mesType'>
          <el-radio-group v-model="formData.mesType" @change="changeMesType">
            <el-radio :label="1">系统</el-radio>
            <el-radio :label="2">私信</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="发送对象" prop="recTargetType"  :error='errorMessage.recTargetType'>
          <el-radio-group v-if="formData.mesType === 2" v-model="formData.recTargetType">
            <el-radio :label="4">指定会员</el-radio>
            <el-radio :label="5">指定商家</el-radio>
          </el-radio-group>
          <el-radio-group v-else v-model="formData.recTargetType">
            <el-radio :label="1">全部</el-radio>
            <el-radio :label="2">会员等级</el-radio>
            <el-radio :label="3">商家等级</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item>
         <el-select v-model="formData.memeberGroupId" placeholder="请选择会员等级"  v-if="formData.recTargetType === 2" >
           <el-option
             v-for="item in memberLevelList"
             :key="item.id"
             :label="item.levelName"
             :value="item.id">
           </el-option>
          </el-select>
         <el-select v-model="formData.storeGroupId" placeholder="请选择商家等级"  v-if="formData.recTargetType === 3" >
           <el-option
             v-for="item in storeLevelList"
             :key="item.id"
             :label="item.levelName"
             :value="item.id">
           </el-option>
          </el-select>
          <cms-member v-else-if="formData.recTargetType === 4" v-model="formData.memeberId"
           labelName='username' :isOpenStore='true'></cms-member>
          <cms-shops v-else-if="formData.recTargetType === 5" v-model="formData.storeId"
           :labelData.sync="formData.storeId"
           labelName='storeName'>
          </cms-shops>
        </el-form-item>
        <el-form-item label="标题" prop="title" :error='errorMessage.title'>
          <el-input v-model.number="formData.title" maxlength="100"></el-input>
        </el-form-item>
        <el-form-item label="内容" prop="content" :error='errorMessage.content'>
          <el-input type="textarea" :rows="5" v-model="formData.content" maxlength="250"></el-input>
        </el-form-item>
      </el-form>
       <div class="cms-form-footer">
         <cms-button @click="save(true)"  v-color >发送</cms-button>
      </div>
    </div>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import { mailSave } from '@/api/setting/sysmessage.js'
import cmsMember from '@/components/cms/cmsMember.vue'
import cmsShops from '@/components/cms/cmsShops.vue'
import { memberlevelList } from '@/api/users/level.js'
import { gradeList } from '@/api/shop/shopLevel.js'
var defaultData = {
  title: '',
  content: '',
  mesType: 1,
  recTargetType: 1,
  memeberGroupId: '',
  storeGroupId: '',
  memeberId: [],
  storeId: []
}
export default {
  name: 'mailSave',
  mixins: [form],
  data() {
    return {
      memberLevelList: [], // 会员等级下拉列表数据
      storeLevelList: [], // 商家等级下拉列表数据
      formData: Object.assign({}, defaultData),
      rules: {// 新增规则
        mesType: [rules.required()],
        recTargetType: [rules.required('请选择发送对象', 'change')],
        title: [rules.required()],
        content: [rules.required(), rules.len(1, 250)],
        memeberGroupId: [rules.required()]
      }
    }
  },
  components: {
    cmsMember,
    cmsShops
  },
  methods: {
    save(state) { // 发送之前（针对发送对象不同还要进行拦截）
      if (this.formData.recTargetType === 2 && this.formData.memeberGroupId === '') {
        this.$errorMessage('请选择会员等级')
      } else if (this.formData.recTargetType === 3 && this.formData.storeGroupId === '') {
        this.$errorMessage('请选择商家等级')
      } else if (this.formData.recTargetType === 4 && this.formData.memeberId.length < 1) {
        this.$errorMessage('请选择指定会员')
      } else if (this.formData.recTargetType === 5 && this.formData.storeId < 1) {
        this.$errorMessage('请选择指定商家')
      } else {
        this.setParams(this.formData.recTargetType)
        this.saveForm(mailSave, this.formData, state, '/mail/list', 'list')
      }
    },
    setParams(type) { // 发送之前需要的置空未选中的发送对象，根据recTargetType
      switch (type) {
        case 1:// 选择全部时(清空所有发送对象)
          this.formData.memeberGroupId = ''
          this.formData.storeGroupId = ''
          this.formData.memeberId = []
          this.formData.storeId = []
          break
        case 2:
          this.formData.storeGroupId = ''
          this.formData.memeberId = []
          this.formData.storeId = []
          break
        case 3:
          this.formData.memeberGroupId = ''
          this.formData.memeberId = []
          this.formData.storeId = []
          break
        case 4:
          this.formData.memeberGroupId = ''
          this.formData.storeGroupId = ''
          this.formData.storeId = []
          break
        case 5:
          this.formData.memeberGroupId = ''
          this.formData.storeGroupId = ''
          this.formData.memeberId = []
          break
      }
    },
    getMemberLevelList() { // 获取会员等级列表
      memberlevelList({ page: 1, size: 20 }).then(res => {
        if (res.code === 200) {
          this.memberLevelList = res.data.content
        } else {
          this.$errorMessage(res.message)
        }
      })
    },
    getStoreLevelList() { // 获取商家等级列表
      gradeList({ page: 1, size: 20 }).then(res => {
        if (res.code === 200) {
          this.storeLevelList = res.data.content
        } else {
          this.$errorMessage(res.message)
        }
      })
    },
    changeMesType(val) { // 消息类型单选框之间的切换
      if (val === 1) {
        this.formData.recTargetType = 1
      } else {
        this.formData.recTargetType = 4
      }
    }
  },
  activated() {
    // 缓存触发
    if (this.$route.query.type === 'save') {
      this.formData = Object.assign({}, defaultData)
    }
  },
  created() {
    this.getMemberLevelList()
    this.getStoreLevelList()
  }
}
</script>
<style scoped>
.el-tag--success {
    margin-right: 5px;
}
</style>




